.page.home {
  .about-us {
    h2 {
      font-size: 50px;
      font-weight: 300;
      color: #444;
      text-transform: uppercase;
      position: relative;
      display: block;
      padding-bottom: 20px;
      line-height: 1.5;

      &:after {
        content: "";
        position: absolute;
        display: block;
        width: 80px;
        height: 2px;
        background: var(--dark);
        left: 50%;
        margin-left: -40px;
        bottom: 0;
      }
    }

    h3 {
      font-weight: 300;
      line-height: 1.5;
      width: 80%;
    }
  }
}
